import { faHourglass } from '@fortawesome/free-solid-svg-icons';
import { ChangeEvent, useState } from 'react';
import { MultiValue, SingleValue } from 'react-select';
import Select from 'react-select'
import { BaseUrl } from '../App';
import { IClimbInfo } from '../Climbs';
import ResponsiveGrid from '../components/ResponsiveGrid';
import Section from '../components/Section';
import Tile from '../components/Tile';
import Profile from '../Profile'; 
import { RatingSystemLabels } from '../RatingSystems';
import { Tags, UniqueTagContraints } from '../Tags';

interface ISelectorOption<TValue = string> {
    value: TValue; 
    label: string; 
}

const MindsetLabels: { [key: string]: string } = { 'hyped': " I'm hyped!", 'scared': " I'm scared!", 'curious': " I'm curious..", 'anxious': " I'm anxious!", 'careful': " I'm careful.", 'confident': " I'm confident!", 'unsure': " I'm unsure..", }; 
const MindsetOptions: ISelectorOption[] = Object.keys(MindsetLabels).map(k => ({ value: k, label: MindsetLabels[k] })); 

const WallTypeLabels: { [key: string]: string } = { 'vertical': " Vertical (90deg)", 'slab': " Slab (<90deg)", 'overhang': " Overhang (>90deg)" }; 
const WallTypeOptions: ISelectorOption[] = Object.keys(WallTypeLabels).map(k => ({ value: k, label: WallTypeLabels[k] })); 

const TagOptions: ISelectorOption[] = Object.keys(Tags).map(t => ({ value: t, label: Tags[t].label })); 

function RecordClimbContent() {
    const [ climb, setClimb ] = useState<IClimbInfo>(getTemplate()); 

    const ratingSystemLabels = RatingSystemLabels[Profile.current.ratingSystem] || RatingSystemLabels['default']; 
    const ratingOptions = ratingSystemLabels.map((l, i) => ({ value: i, label: l } as ISelectorOption<number>)); 

    const tagOptions = (climb.tags || []).map(t => ({ value: t, label: (Tags[t] && Tags[t].label) || t } as ISelectorOption)); 

    return (<>

        <Section headline={{ text: "You are attempting this climb", icon: faHourglass }}>
            <fieldset>
                <div>
                    <label><span className="label">Difficulty Rating</span>
                        <Select
                            className="selector"
                            classNamePrefix="selector"
                            value={{ value: climb.difficulty, label: ratingSystemLabels[climb.difficulty] } as ISelectorOption<number>}
                            options={ratingOptions}
                            onChange={editDifficulty}
                        />
                    </label>
                </div>
                <div>
                    <label><span className="label">Wall location</span>
                        <input type="text" maxLength={50} value={climb.wall} onChange={editWall} placeholder="Which wall are you climbing?" />
                    </label>
                </div>
                <div>
                    <label><span className="label">Type of wall</span>
                        <Select
                            className="selector"
                            classNamePrefix="selector"
                            value={{ value: climb.wallType, label: WallTypeLabels[climb.wallType] } as ISelectorOption}
                            options={WallTypeOptions}
                            onChange={editWallType}
                        />
                    </label>
                </div>
                <div>
                    <label><span className="label">Mindset before climb</span>
                        <Select
                            className="selector"
                            classNamePrefix="selector"
                            value={{ value: climb.mindset || 'hyped', label: MindsetLabels[climb.mindset || 'hyped'] } as ISelectorOption}
                            options={MindsetOptions}
                            onChange={editMindset}
                        />
                    </label>
                </div>
                <div>
                    <label><span className="label">Remarks</span>
                        <input type="text" maxLength={250} value={climb.remarks} onChange={editRemarks} placeholder="Space for your personal notes, e.g. about emotions experienced, technique used etc." />
                    </label>
                </div>
                <div>
                    <label><span className="label">Features &amp; achievements</span>
                        <Select
                            isMulti
                            className="selector"
                            classNamePrefix="selector"
                            value={tagOptions}
                            options={TagOptions}
                            onChange={editTags}
                        />
                    </label>
                </div>
            </fieldset>

            <p>Take care and record your result:</p>
            <ResponsiveGrid colsLandscape={3} colsPortrait={1} compact>
                <Tile mainText="" subText="I FAILED" onClick={doRecordFail} />
                <Tile mainText="" subText="I PASSED!" onClick={doRecordPass} />
                <Tile mainText="" subText="CANCEL" onClick={doCancel} />
            </ResponsiveGrid>
        </Section>

    </>);

    function getTemplate(): IClimbInfo {
        // if specified via URL: 
        if (window.location.hash) {
            try {
                const json = JSON.parse(decodeURIComponent(window.location.hash.substring(1)));
                if (typeof (json) === 'object')
                    return json;
            }
            catch (e) { console.log(e); }
        }
        // otherwise default settings based on previous climbing progress: 
        const currentProfile = Profile.current;
        const allClimbs = currentProfile.getClimbs(); 
        const previousClimb = allClimbs.length ? allClimbs[allClimbs.length - 1] : null; 
        return ({ 
            date: new Date(), 
            successful: false, 
            wall: previousClimb ? previousClimb.wall : "My gym", 
            wallType: previousClimb ? previousClimb.wallType : 'vertical', 
            difficulty: previousClimb ? previousClimb.difficulty : 1, 
            tags: [], 
            remarks: '', 
            mindset: 'hyped', 
            attempt: 0, 
            streak: 0, 
        });
    }

    function editWall(ev: ChangeEvent<HTMLInputElement>) {
        setClimb({ ...climb, wall: ev.target.value });  // re-renders
    }

    function editWallType(option: SingleValue<ISelectorOption>) {
        setClimb({ ...climb, wallType: (option && option.value as any) || 'vertical' });  // re-renders
    }

    function editDifficulty(option: SingleValue<ISelectorOption<number>>) {
        setClimb({ ...climb, difficulty: (option && option.value) || 0 });  // re-renders
    }

    function editTags(options: MultiValue<ISelectorOption>) {
        const newTags = (options && options.length) ? options.map(o => o.value) : []; 
        // validate: 
        for (const prefix in UniqueTagContraints)
            if (newTags.filter(t => t.indexOf(prefix) === 0).length > 1) {
                alert(UniqueTagContraints[prefix]); 
                return; 
            }
        setClimb({ ...climb, tags: newTags });  // re-renders
    }

    function editMindset(option: SingleValue<ISelectorOption>) {
        setClimb({ ...climb, mindset: (option && option.value) || '' });  // re-renders
    }

    function editRemarks(ev: ChangeEvent<HTMLInputElement>) {
        setClimb({ ...climb, remarks: ev.target.value });  // re-renders
    }

    function doCancel() {
        window.history.back();
    }

    function doRecordFail() {
        if (climb.tags.filter(t => t.indexOf('pass_') === 0).length > 0) {
            alert("You cannot select a PASS type achievement if you failed the climb (remove the tag Onsight/Red-Point/Hang-Dog)."); 
            return; 
        }
        const FailMessages = [ "Don't worry, try again.", "Happens to the best of us.", "Nevermind, keep going.", "Exercise makes the master.", "Can't have them all.", "Better luck next time.", "Don't give up!", "Take it easy." ]; 
        const msg = FailMessages[Math.round(Math.random() * (FailMessages.length - 1))]; 
        if (window.confirm(msg)) {
            climb.date = new Date(); 
            climb.successful = false; 
            Profile.current.addClimb(climb); 
            window.location.replace(BaseUrl + '/');  // go to Dashboard
        }
    }

    function doRecordPass() {
        const SuccessMessages = [ "Well done!", "Congratulations!", "Nice one, keep going!", "Fantastic.", "You're doing fine today!", "practiced!", "Great, now have a rest.", "Respect.", "Ready for the next challenge?" ]; 
        const msg = SuccessMessages[Math.round(Math.random() * (SuccessMessages.length - 1))]; 
        if (window.confirm(msg)) {
            climb.date = new Date(); 
            climb.successful = true; 
            Profile.current.addClimb(climb); 
            window.location.replace(BaseUrl + '/');  // go to Dashboard
        }
    }
}

export default RecordClimbContent;
