import { IClimbData, IClimbInfo } from "./Climbs";
import { RatingSystemLabels, RatingSystemNames } from "./RatingSystems";
import { UserData } from "./UserData";

/** Manages a user profile (loads from and writes to persisted data store). */
export default class Profile {

    private static currentProfile: Profile; 
    private data: IProfileData; 

    /** Returns the current profile. */
    static get current(): Profile {
        if (!this.currentProfile)
            this.currentProfile = new Profile(UserData.getActiveProfile()); 
        return this.currentProfile; 
    }

    /** Switches the current profile (internally, does not update UI). */
    static set current(newProfile: Profile) {
        this.currentProfile = newProfile; 
    }

    /** Loads a profile */
    constructor(profileName: string) {
        this.data = UserData.getProfileData(profileName);  // load
        if ((this.name.indexOf('Demo') !== -1) && !this.data.climbs.length && (new Date().valueOf() - new Date(this.data.joined).valueOf() < 1))  // just created
            this.addDemoClimbs(); 
    }

    get name(): string {
        return this.data.name; 
    }

    rename(newName: string) {
        this.data.name = UserData.renameProfile(this.name, newName); 
    }

    get joined(): Date {
        return new Date(this.data.joined); 
    }

    get ratingSystem(): keyof typeof RatingSystemNames {
        return this.data.ratingSystem as any || 'default'; 
    }

    set ratingSystem(system: keyof typeof RatingSystemNames) {
        this.data.ratingSystem = (system && RatingSystemLabels.hasOwnProperty(system)) ? system : 'default'; 
    }

    getDifficultyRatingLabel(difficulty: number): string {
        const labels = RatingSystemLabels[this.ratingSystem] as string[] || RatingSystemLabels['default']; 
        if (difficulty < 0)
            return 'None'; 
        if (difficulty > labels.length)
            return ''; 
        return labels[difficulty]; 
    }

    getClimbs(): Readonly<IClimbInfo>[] {
        let currentDifficulty = 0; 
        let currentAttempt = 0; 
        let currentStreak = 0; 
        const climbs = this.data.climbs.map(c => {
            if (c.difficulty !== currentDifficulty) {
                currentDifficulty = c.difficulty; 
                currentAttempt = 1; 
            } else
                currentAttempt++; 
            
            const climbInfo: IClimbInfo = {
                date: new Date(c.date), 
                successful: c.successful, 
                wall: c.wall || 'Unknown', 
                wallType: c.wallType || 'vertical', 
                difficulty: c.difficulty, 
                tags: c.tags || [], 
                remarks: c.remarks || '', 
                mindset: c.mindset || '', 
                attempt: currentAttempt, 
                streak: currentStreak
            }; 

            if (c.successful) {
                currentStreak++; 
                currentAttempt = 0; 
            } else
                currentStreak = 0; 
            
            return climbInfo; 
        }); 
        return climbs; 
    }

    addClimb(climb: IClimbInfo) {
        if (!climb)
            return; 
        this.data.climbs.push({ 
            date: (climb.date || new Date()).toISOString(), 
            successful: !!climb.successful, 
            wall: climb.wall || 'Unknown', 
            wallType: climb.wallType || 'vertical', 
            difficulty: Number(climb.difficulty) || 1, 
            tags: climb.tags || [], 
            remarks: climb.remarks || '', 
            mindset: climb.mindset || '', 
        } as IClimbData); 
        this.data.climbs.sort((a,b) => new Date(a.date).valueOf() - new Date(b.date).valueOf());  // make sure they come in chronological order
        this.save(); 
    }

    private addDemoClimbs() {
        let climbLevel = 1; 
        let climbScore = 0; 
        this.data.climbs = [];  // clear
        const skill = Math.random() * 3; 
        for (let i=0; i<30 && climbLevel < 32; i++) {  // climbs
            const success = (Math.random() < 1.0 / Math.max(1.0, climbLevel / skill));  // decreasing % of success with difficulty
            if (success)
                climbScore++; 
            this.data.climbs.push({ 
                date: '',  // see below
                successful: success, 
                wall: 'Demo Wall', 
                wallType: 'vertical', 
                difficulty: climbLevel, 
                tags: [], 
                remarks: '', 
                mindset: '', 
            }); 
            if (climbScore >= 10) {
                climbLevel++; 
                climbScore = 0; 
            }
        }
        let daysAgo = 0; 
        for (let i=this.data.climbs.length - 1; i>=0; i--) {
            this.data.climbs[i].date = new Date(new Date().valueOf() - 1000*60*60*24*daysAgo).toISOString();  // N days daysAgo
            daysAgo += Math.random() * ((Math.random() < 0.35) ? 9 : 1);  // end of training day or not (pauses days or hours)
        }
    }

    private save() {
        UserData.setProfileData(this.data);  // store
    }
}

/** Structure containing persisted profile data */
export interface IProfileData {
    name: string; 
    joined: string;  // Date.toISOString()
    climbs: IClimbData[]; 
    ratingSystem: string;  // 'default'
}
