import { BrowserRouter, Routes, Route } from "react-router-dom";
import Page from './layout/Page'; 
import DashboardContent from './content/Dashboard';
import ClimbHistoryContent from "./content/ClimbHistoryContent";
import StartNewClimbContent from "./content/StartNewClimbContent";
import RecordClimbContent from "./content/RecordClimbContent";
import FeedbackContent from "./content/FeedbackContent";
import AboutContent from "./content/AboutContent";
import TermsContent from "./content/TermsContent";

export const BaseUrl = "/climbify-app";  // hosting location

function App() {
  // Noob: The .tsx extension is TypeScript (.ts) + React extensions (similar to .jsx), so you can write HTML-like structures,  
  //       such as <div className="App">, which expands to `react.createElement('div', { class: 'App' }, children))`, 
  //       a Javascript object tree. 
  //       This is a render function (a.k.a. React component), which returns a shadow DOM (the tree of objects), which 
  //       React compares to the previous rendering output, in order to only adjust the parts of the browser DOM that need adjusting. 
  //       Non-HTML elements can be defined in the same way as tags, e.g. <MyComponent ... />, then referring to another 
  //       render function that takes props (the attributes) and children (the content) and is defined in some imported .js file. 

  // Routing: Depending on the current path in the URL we render different contents (i.e. sub components). 
  //          The BrowserRouter component watches for URL changes and rerenders itself, we only need to define the rules. 
  // Page template: All pages have the same footer and header layout, so we wrap them into the same Layout component: 
  return (  
    <BrowserRouter basename={BaseUrl}>
      <Routes>
        <Route path="/" element={ <Page /> }>
          <Route index element={ <DashboardContent /> } />
          <Route path="climb" element={ <StartNewClimbContent /> } />
          <Route path="record" element={ <RecordClimbContent /> } />
          <Route path="history" element={ <ClimbHistoryContent /> } />
          <Route path="feedback" element={ <FeedbackContent /> } />
          <Route path="about" element={ <AboutContent /> } />
          <Route path="terms" element={ <TermsContent /> } />
          <Route path="*" element={ <DashboardContent /> } />
        </Route>
      </Routes>
    </BrowserRouter>
  ); 
}

export default App;
