import './Tile.css';  // loader embeds the CSS into the page (classes are globally available)

export interface ITileProps {
    mainText: string | React.ReactNode; 
    subText?: string | React.ReactNode; 
    onClick?: () => void; 
    height?: number | string; 
}

function Tile(props: ITileProps) {
    return (
        <button type="button" className="tile" onClick={ props.onClick } style={{ height: props.height }}>
            { props.subText && <p className="tile-sub">{ props.subText }</p> }
            <h3 className={ ((typeof(props.mainText) !== 'string') || (props.mainText.length > 3)) ? 'small' : undefined }>{ props.mainText }</h3>
        </button>
    ); 
}

export default Tile; 