import { NavLink } from "react-router-dom";
import Avatar from "react-avatar";
import Select from 'react-select'
import { components, ControlProps, OptionProps, SingleValue, DropdownIndicatorProps } from 'react-select';
import { UserData } from "../UserData";

const ProfileOption = (props: OptionProps<profileOption, false>) => {
  return (
    <components.Option {...props}>
      <Avatar round name={props.data.value} className="avatar" /><h4>{props.data.value}</h4>
    </components.Option>
  );
};

export interface profileOption {
  readonly value: string;
  readonly label: string;
}

export const profileOptions: readonly profileOption[] = UserData.getProfiles().map((username) => { return {value: username, label: username} });

function getOptionFromUsername(username: string) {
  return profileOptions.find((option) => option.value === username);
}

const DropdownIndicator = (
  props: DropdownIndicatorProps<profileOption, false>
) => {
  return (
    <components.DropdownIndicator {...props}>
      <i></i>
    </components.DropdownIndicator>
  );
};

const customStyles = {
  singleValue: (base: any) => ({ ...base, color: '#9d71e8' }),
  control: () => ({
    width: 'auto',
    'margin-left': '1em',
    'margin-right': '1em',
  }),
  menuList: (base: any) => ({
    ...base,
    height: "40em",
    // Firefox does not have this
    "::-webkit-scrollbar": {
      width: "9px"
    },
    "::-webkit-scrollbar-track": {
      background: "#9d71e8"
    },
    "::-webkit-scrollbar-thumb": {
      background: "#888"
    },
    "::-webkit-scrollbar-thumb:hover": {
      background: "#555"
    },
  }),
}

const ProfileControl = ({ children, ...props }: ControlProps<profileOption, false>) => {
  let profile = UserData.getActiveProfile();
  return (
    <components.Control {...props}>
      <Avatar round name={profile} className="avatar" />
      {children}
    </components.Control>
  );
};


// Note: we must use <Link to> or <NavLink to> instead of <a href> for internal links in order to have rerendering without actual page navigation. 
//       It will do the rest for us (rerendering the main BrowserRouter component and adjust the browser page URL via the URL API). 

function Navigation() {
  return (
    <nav className="navigation">
      <ul>
        <li><NavLink to="/" className={determineClass}>Dashboard</NavLink></li>
        <li><NavLink to="/climb" className={determineClass}>New Climb</NavLink></li>
        <li><NavLink to="/history" className={determineClass}>My History</NavLink></li>
        <li><NavLink to="/feedback" className={determineClass}>Feedback</NavLink></li>
        <li><NavLink to="/about#company" className={determineClass}>About</NavLink></li>
        <li><Select
          className="basic-single"
          classNamePrefix="select"
          isDisabled={false}
          isLoading={false}
          isClearable={false}
          isRtl={false}
          aria-live="polite"
          isSearchable={false}
          name="usernameAvatar"
          defaultValue={getOptionFromUsername(UserData.getActiveProfile())}
          options={profileOptions}
          components={{ DropdownIndicator, Option: ProfileOption, Control: ProfileControl }}
          styles={customStyles}
          controlShouldRenderValue={true}
          hideSelectedOptions={false}
          onChange={(option: SingleValue<profileOption>) => {
            if (option) {
              UserData.switchProfile(option.value);
              // Maybe reload only components not in footer/header
              window.location.reload();
            }
          }}
        />
        </li>
      </ul>
    </nav>
  ); 
}

function determineClass(props: { isActive: boolean }) {  // called by NavLink
  return props.isActive ? 'current' : ''; 
}

export default Navigation;
