/**
 * Tags are achievements and features of a climb and are also used to compute suggestions. 
 * Associated points are in the range from -100 to +100 (where 100 means the climber should advance to the next level and -100 means he/she should go back). 
 * There are 32 levels, and all tags add up, so having +300 means to advance 3 levels, e.g. from 5c to 6b in the French system. 
 */
export const Tags: { [key: string]: { points: number, label: string } } = {    
    // max. one of: 
    'pass_hangdog':          { points:   80, label: " Hang Dog (completed with pauses, hanging on rope)" }, 
    'pass_redpoint':         { points:  100, label: " Red Point (completed in one Go)" }, 
    'pass_onsight':          { points:  100, label: " Onsight! (completed in one Go at first attempt)" }, 

    // max. one of: 
    'technique_bouldering':  { points:  100, label: " Bouldering (no gear/safety)" }, 
    'technique_leading':     { points:   80, label: " Leading (rope)" }, 
    'technique_top_roping':  { points:   50, label: " Top Roping (rope, assisted)" }, 

    'hold_jug_practiced':    { points:   10, label: " Jug holds practiced" }, 
    'hold_pinch_practiced':  { points:   20, label: " Pinch holds practiced" }, 
    'hold_crimp_practiced':  { points:   30, label: " Crimp holds practiced" }, 
    'hold_sloper_practiced': { points:   40, label: " Sloper holds practiced" }, 
    'hold_pocket_practiced': { points:   50, label: " Pocket holds practiced" }, 
    'hold_jib_practiced':    { points:   60, label: " Jib holds practiced" }, 
    'hold_mono_practiced':   { points:   70, label: " Mono holds practiced" }, 

    'hold_jug_failed':       { points:  -10, label: " Jug holds problematic" }, 
    'hold_pinch_failed':     { points:  -20, label: " Pinch holds problematic" }, 
    'hold_crimp_failed':     { points:  -30, label: " Crimp holds problematic" }, 
    'hold_sloper_failed':    { points:  -40, label: " Sloper holds problematic" }, 
    'hold_pocket_failed':    { points:  -50, label: " Pocket holds problematic" }, 
    'hold_jib_failed':       { points:  -60, label: " Jib holds problematic" }, 
    'hold_mono_failed':      { points:  -70, label: " Mono holds problematic" }, 

    // TODO: add more techniques & achievements
}; 

