export const UniqueTagContraints: { [keyPrefix: string]: string/*error message*/ } = {
    'pass_': "You cannot select more than one PASS type. Remove the other tag first (either Hang-Dog, Red-Point or Onsight).", 
    'technique_': "You cannot select more than one TECHNIQUE. Remove the other tag first (either Bouldering, Leading or Top-Roping).", 
}; 

