import { faAdd, faGaugeHigh, faGaugeMed, faGaugeSimple } from '@fortawesome/free-solid-svg-icons';
import { useNavigate } from 'react-router-dom';
import { IClimbInfo } from '../Climbs';
import ResponsiveGrid from '../components/ResponsiveGrid';
import Section from '../components/Section';
import Tile from '../components/Tile';
import Profile from '../Profile';

function StartNewClimbContent() {
  const navigate = useNavigate(); 

  const currentProfile = Profile.current; 
  const allClimbs = currentProfile.getClimbs(); 
  const currentDifficulty = allClimbs.length ? allClimbs[allClimbs.length - 1].difficulty : 1; 

  const easyClimbs: IClimbInfo[] = [ 
    { date: new Date(), successful: false, wall: "Wall A", wallType: 'vertical', tags: [], difficulty: currentDifficulty - 1, remarks: '', mindset: '', attempt: 0, streak: 0 }, 
    { date: new Date(), successful: false, wall: "Wall B", wallType: 'vertical', tags: [], difficulty: currentDifficulty - 1, remarks: '', mindset: '', attempt: 0, streak: 0 }, 
    { date: new Date(), successful: false, wall: "Wall C", wallType: 'vertical', tags: [], difficulty: currentDifficulty - 1, remarks: '', mindset: '', attempt: 0, streak: 0 }, 
    { date: new Date(), successful: false, wall: "Wall D", wallType: 'vertical', tags: [], difficulty: currentDifficulty - 1, remarks: '', mindset: '', attempt: 0, streak: 0 }, 
  ]; 
  const mediumClimbs: IClimbInfo[] = [ 
    { date: new Date(), successful: false, wall: "Wall A", wallType: 'vertical', tags: [], difficulty: currentDifficulty, remarks: '', mindset: '', attempt: 0, streak: 0 }, 
    { date: new Date(), successful: false, wall: "Wall B", wallType: 'vertical', tags: [], difficulty: currentDifficulty, remarks: '', mindset: '', attempt: 0, streak: 0 }, 
    { date: new Date(), successful: false, wall: "Wall C", wallType: 'vertical', tags: [], difficulty: currentDifficulty, remarks: '', mindset: '', attempt: 0, streak: 0 }, 
    { date: new Date(), successful: false, wall: "Wall D", wallType: 'vertical', tags: [], difficulty: currentDifficulty, remarks: '', mindset: '', attempt: 0, streak: 0 }, 
  ]; 
  const hardClimbs: IClimbInfo[] = [ 
    { date: new Date(), successful: false, wall: "Wall A", wallType: 'vertical', tags: [], difficulty: currentDifficulty + 1, remarks: '', mindset: '', attempt: 0, streak: 0 }, 
    { date: new Date(), successful: false, wall: "Wall B", wallType: 'vertical', tags: [], difficulty: currentDifficulty + 1, remarks: '', mindset: '', attempt: 0, streak: 0 }, 
    { date: new Date(), successful: false, wall: "Wall C", wallType: 'vertical', tags: [], difficulty: currentDifficulty + 1, remarks: '', mindset: '', attempt: 0, streak: 0 }, 
    { date: new Date(), successful: false, wall: "Wall D", wallType: 'vertical', tags: [], difficulty: currentDifficulty + 1, remarks: '', mindset: '', attempt: 0, streak: 0 }, 
  ]; 
  const template: IClimbInfo = { date: new Date(), successful: false, wall: "Custom", wallType: 'vertical', tags: [], difficulty: currentDifficulty, remarks: '', mindset: 'Scary?', attempt: 0, streak: 0 }; 

  return (<>

    <Section headline={ { text: "Easy - for warm up", icon: faGaugeSimple } } anchor="easy">
      <p>Your training should start with an easier warm-up (with difficulty from previous levels). We suggest the following for starting your day:</p>
      <ResponsiveGrid colsLandscape={4} colsPortrait={1} compact>
        { easyClimbs.map((c, i) => <Tile mainText={ c.wall } subText={ `Rating: ${ Profile.current.getDifficultyRatingLabel(c.difficulty) }` } onClick={ () => startClimb(c) } key={i} />) }
      </ResponsiveGrid>
    </Section>

    <Section headline={ { text: "Medium - for review", icon: faGaugeMed } } anchor="medium">
      <p>To solidify your technique you may want to try the following suggestions, which build up on your current skill:</p>
      <ResponsiveGrid colsLandscape={4} colsPortrait={1} compact>
        { mediumClimbs.map((c, i) => <Tile mainText={ c.wall } subText={ `Rating: ${ Profile.current.getDifficultyRatingLabel(c.difficulty) }` } onClick={ () => startClimb(c) } key={i} />) }
      </ResponsiveGrid>
    </Section>

    <Section headline={ { text: "Hard - challenge", icon: faGaugeHigh } } anchor="hard">
      <p>If you feel ready to progress to the next level, here is what you have to master:</p>
      <ResponsiveGrid colsLandscape={4} colsPortrait={1} compact>
        { hardClimbs.map((c, i) => <Tile mainText={ c.wall } subText={ `Rating: ${ Profile.current.getDifficultyRatingLabel(c.difficulty) }` } onClick={ () => startClimb(c) } key={i} />) }
      </ResponsiveGrid>
    </Section>

    <Section headline={ { text: "Custom", icon: faAdd } } anchor="new">
      <p>Use this to create a climb not listed, or record one that you already performed (make sure to record failed climbs as well to see your real progress):</p>

      <ResponsiveGrid colsLandscape={1} colsPortrait={1} compact>
        <Tile mainText="Custom Climb..." subText={ "Record your climb" } onClick={ () => startClimb(template) } />
      </ResponsiveGrid>
    </Section>

  </>); 

  function startClimb(climb: IClimbInfo) {
    navigate('/record#' + JSON.stringify(climb)); 
  }
}

export default StartNewClimbContent;
