import './Section.css';  // loader embeds the CSS into the page (classes are globally available)
import Headline, { IHeadlineProps } from "./Headline";

/** The properties (and their type definition) which the `Section` component takes */
export interface ISectionProps {
  
  /** Optional - The headline to show */
  headline?: string | IHeadlineProps; 

  /** The contents to render */
  children: React.ReactNode; 

  /** Optional - A unique name for an anchor to wrap the section into (for #jumping to via URL). */
  anchor?: string; 

}

function Section(props: ISectionProps) {
  let headline; 
  if (props.headline) {
    if (typeof(props.headline) === 'object')
      headline = <Headline { ...props.headline } />; 
    else
      headline = <Headline text={props.headline} />; 
  }

  const content = props.children; 

  return (
    <section className="section" id={ props.anchor }>
      { headline }
      { content }
    </section>
  ); 
}

export default Section;
