import { faChartLine, faCheck, faHistory, faChartBar } from "@fortawesome/free-solid-svg-icons";
import { Link } from "react-router-dom";
import ClimbHistoryListing from "../components/ClimbHistoryListing";
import Headline from "../components/Headline";
import PieChart from "../components/PieChart";
import ProgressChart from "../components/ProgressChart";
import ResponsiveGrid from "../components/ResponsiveGrid";
import Tile from "../components/Tile";
import { useNavigate } from 'react-router-dom'; 

function DashboardContent() {
  const navigate = useNavigate(); 

  return (
    <ResponsiveGrid colsLandscape={2} colsPortrait={1} wideContent>

      <div>
        <Headline text="My Progress" icon={faChartLine} />
        <ProgressChart />
      </div>
      
      <div>
        <Headline text="Next Climb Suggestions" icon={faCheck} />
        <ResponsiveGrid colsLandscape={3} colsPortrait={1} compact>
          <Tile mainText="Easy" subText="Warm-Up" height={220} onClick={ selectEasyClimb } />
          <Tile mainText="Medium" subText="Review" height={220} onClick={ selectMediumClimb } />
          <Tile mainText="Hard" subText="Challenge" height={220} onClick={ selectHardClimb } />
        </ResponsiveGrid>
        <Link to="/record" className="link-button">Start a custom climb...</Link>
      </div>

      <div>
        <Headline text="Climb History" icon={faHistory} />
        <ResponsiveGrid colsLandscape={1} colsPortrait={1} compact>
          <ClimbHistoryListing limit={10} />
        </ResponsiveGrid>
        <Link to="/history" className="link-button">View full History...</Link>
      </div>

      <div>
        <Headline text="Your stats" icon={faChartBar} />
        <ResponsiveGrid colsLandscape={2} colsPortrait={1} compact>
          <PieChart title="Level progress" values={[ 3, 7 ]} labels={[ "Passed", "To go" ]} colorSet="blackWhite" size={250} />
          <PieChart title="Level performance" values={[ 6, 4 ]} labels={[ "Successes", "Failures" ]} colorSet="redGreen" size={250} />
          <PieChart title="Total progress" values={[ 75, 320 ]} labels={[ "Score", "To go" ]} colorSet="blackWhite" size={250} />
          <PieChart title="Total performance" values={[ 6, 4 ]} labels={[ "Successes", "Failures" ]} colorSet="redGreen" size={250} />
        </ResponsiveGrid>
      </div>

    </ResponsiveGrid>
  ); 
  
  function selectEasyClimb() {
    navigate('/climb#easy'); 
  }
  
  function selectMediumClimb() {
    navigate('/climb#medium'); 
  }
  
  function selectHardClimb() {
    navigate('/climb#hard'); 
  }
}

export default DashboardContent;
