import { Link } from "react-router-dom";
import BrandLogo from "./BrandLogo";
import Section from "../components/Section";

// Note: we must use <Link to> instead of <a href> for internal links in order to have rerendering without actual page navigation. 
//       It will do the rest for us (rerendering the main BrowserRouter component and adjust the browser page URL via the URL API). 

function PageFooter() {
  return (
    <footer>
      <div style={{ gridArea: 'logo' }}>
        <BrandLogo iconOnly />
      </div>

      <div style={{ gridArea: 'about' }}>
        <Section headline="About us">
          Climbify is dedicated to your progress in climbing. <br/>
          This tool will help you reach your goals. 
        </Section>
      </div>

      <div style={{ gridArea: 'company' }}>
        <Section headline="Company">
          Climbify Ltd.<br/>
          Secret Way 1<br/>
          London
        </Section>
      </div>

      <div style={{ gridArea: 'legal' }}>
        <Section headline="Legal">
          <ul>
            <li><Link to="terms#privacy">Privacy policy</Link></li>
            <li><Link to="terms#service">Terms of service</Link></li>
            <li><Link to="about#contact">Contact</Link></li>
            <li><Link to="about#company">Imprint</Link></li>
          </ul>
        </Section>
      </div>

      <div style={{ gridArea: 'social' }}>
        <Section headline="Social">
          <ul>
            <li><a href="https://instagram.com/climbify" target="_blank" rel="noreferrer">Instagram</a></li>
            <li><a href="https://facebook.com/climbify" target="_blank" rel="noreferrer">Facebook</a></li>
            <li><a href="https://twitter.com/@climbify" target="_blank" rel="noreferrer">Twitter</a></li>
          </ul>
        </Section>
      </div>

    </footer>
  ); 
}

export default PageFooter;
