import './Headline.css';  // loader embeds the CSS into the page (classes are globally available)
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome"; 
import { IconDefinition } from "@fortawesome/free-solid-svg-icons";
// see https://www.delftstack.com/howto/react/font-awesome-react/

/** The properties (and their type definition) which the `Section` component takes */
export interface IHeadlineProps {
  
  /** The text to show */
  text: string | React.ReactNode; 

  /** Optional - The icon to show (this can be an URL or data-URI, e.g. imported from an asset). */
  icon?: IconDefinition; 
}

function Headline(props: IHeadlineProps) {
  let output = <h2>{ props.text }</h2>; 

  if (props.icon) {
    output = 
      <div className="headline">
        <FontAwesomeIcon icon={props.icon} className="icon" />
        { output }
      </div>; 
  }

  return output; 
}

export default Headline;
