// Rating systems mapping (difficulty is a number from 0 to 32 and different systems provide different labels to show)
// Each user can select his/her preferred system, which is a persisted Profile setting (use `Profile.current.getDifficultyRatingLabel(n)` to retrieve). 

const FrenchRatingSystemLabels = [ 
    "None",  // 0 - Visitor
    "1", "2", "3",  // 1,2,3 - Novice
    "4a", "4b", "4c", "5a",  // 4,5,6,7 - Beginner
    "5b", "5c", "6a", "6a+", "6b",  // 8,9,10,11,12 - Intermediate
    "6b+", "6c", "6c+", "7a", "7a+",  // 13,14,15,16,17 - Advanced
    "7b", "7b+", "7c", "7c+",  // 18,19,20,21 - Expert
    "8a", "8a+",  // 22,23 - Super Expert
    "8b", "8b+", "8c", "8c+",  // 24,25,26,27 - Elite
    "9a", "9a+", "9b", "9b+",  // 28,29,30,31 - Super Elite
    "9c",  // 32 - Aliens
]; 

// TODO: add more systems

/** Display names for the supported difficulty rating systems */
export const RatingSystemNames = {
    'default': 'French', 
    'french': 'French', 

}; 

/** Rating labels for the supported difficulty rating systems */
export const RatingSystemLabels: { [systemName in keyof typeof RatingSystemNames]: string[] } = {
    'default': FrenchRatingSystemLabels, 
    'french': FrenchRatingSystemLabels, 

}; 

