import { IProfileData } from "./Profile";

/** API for accessing persisted data. */
export class UserData {

    private static activeProfile: string; 

    /** Returns the name of the currently selected profile (defaults to the default profile "Me" if no profiles are available). */
    static getActiveProfile(): string {
        if (!this.activeProfile) {  // initialize once: 
            let persistedChoice = localStorage.getItem('activeProfile') as string; 
            if (!persistedChoice) {
                persistedChoice = this.getProfiles()[0] || "Me"; 
                this.switchProfile(persistedChoice); 
            }
            this.activeProfile = persistedChoice; 
        }
        return this.activeProfile; 
    }

    /** Switches the currently selected profile (requires re-rendering of the page components to become effective visually). */
    static switchProfile(switchToName: string) {
        if (!switchToName || (switchToName === this.activeProfile))
            return;  // nothing to do
        localStorage.setItem('activeProfile', switchToName); 
        this.activeProfile = switchToName; 
    }

    /** Changes the name of the given profile (adds a numeric suffix if new name exists already) and returns the new final name. */
    static renameProfile(name: string, newName: string): string {
        // avoid duplicates: 
        let n = 1; 
        const existingNames = this.getProfiles().map(n => n.trim().toLowerCase()); 
        let newName2 = newName; 
        while (existingNames.indexOf(newName.trim().toLowerCase()) !== -1)
            newName2 = newName + (++n); 
        newName = newName2; 
        // rename persisted entry: 
        const persistedData = this.getProfileData(name) as IProfileData; 
        if (persistedData) {
            persistedData.name = newName; 
            this.setProfileData(persistedData); 
            localStorage.removeItem('profile_' + name); 
        }
        // if active, change: 
        if (this.activeProfile === name)
            this.activeProfile = newName; 
        return newName; 
    }

    /** Returns the names of all registered profiles. @see getProfileData to retrieve the corresponding profile data. */
    static getProfiles(): string[] {
        let profiles: string[] = []; 
        for (let i=0; i<localStorage.length; i++) {
            const key = localStorage.key(i); 
            if (key && (key.indexOf('profile_') === 0))
                profiles.push(key.substring('profile_'.length)); 
        }
        if (!profiles.length)
            profiles = [ 'Carlos Demo', 'Derek Demo', 'Luke Demo', 'Marco Demo', 'Matt Demo', 'Ruy Demo' ]; 
        return profiles; 
    }

    /** Returns the data for the specified profile (or creates a new profile if not existing, or returns the active profile data, if no name was specified). */
    static getProfileData(profileName?: string): Readonly<IProfileData> {
        if (!profileName)
            profileName = this.getActiveProfile(); 
        let profileData: IProfileData | undefined; 
        const persistedData = localStorage.getItem('profile_' + profileName) as string; 
        if (persistedData) {
            try { profileData = JSON.parse(persistedData); }
            catch (e) {}
        }
        if (typeof(profileData) !== 'object')
            profileData = { name: profileName!, climbs: [], joined: new Date().toISOString(), ratingSystem: 'default' }; 
        return profileData; 
    }

    static setProfileData(profileData: IProfileData) {
        if (!profileData || !profileData.name)
            return; 
        localStorage.setItem('profile_' + profileData.name, JSON.stringify(profileData)); 
    }
}

