import './Listing.css';  // loader embeds the CSS into the page (classes are globally available)
import Profile from "../Profile";
import { Link } from 'react-router-dom';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faArrowRight, faInfoCircle } from '@fortawesome/free-solid-svg-icons';
import { Tags } from '../Tags';

export interface IClimbHistoryListingProps {
    /** If only a number of recent climbs shall be displayed. */
    limit?: number; 
}

function ClimbHistoryListing(props: IClimbHistoryListingProps) {
    const currentProfile = Profile.current; 
    const climbs = currentProfile.getClimbs().reverse(); 
    const rows: JSX.Element[] = []; 
    const now = new Date(); 
    for (let i=0; i<climbs.length; i++) {
        if (props.limit && (i >= props.limit))
            break; 
        const climb = climbs[i]; 
        const daysPassed = Math.round((now.valueOf() - climb.date.valueOf()) / (1000*60*60*24)); 
        const when = (daysPassed < 1) ? "Today" : (daysPassed === 1) ? "Yesterday" : daysPassed + " days ago"; 
        let info = climb.remarks || ''; 
        if (climb.mindset) 
            info = `Mindset: ${climb.mindset}\n${info}`; 
        if (climb.tags && climb.tags.length) 
            info = `Achievements: ${ climb.tags.map(t => (Tags[t] && Tags[t].label) || t ).join(', ') }\n${info}`; 
        let climbResult = climb.successful ? " Pass" : " Fail"; 
        if (climb.successful && climb.tags) {
            if (climb.tags.indexOf('pass_onsight') !== -1)
                climbResult = " Onsight!"; 
            else if (climb.tags.indexOf('pass_redpoint') !== -1)
                climbResult = " Red-Point!"; 
            else if (climb.tags.indexOf('pass_hangdog') !== -1)
                climbResult = " Hang-Dog"; 
        }
        const profile = Profile.current; 
        const row = 
            <tr key={ 'row' + (i + 1) }>
                <td>{ climb.wall }</td>
                <td>{ climbResult } { info && <FontAwesomeIcon icon={faInfoCircle} className="icon" title={info} onClick={ () => { alert(info); } } /> }</td>
                <td>{ climb.attempt }</td>
                <td>{ climb.streak }</td>
                <td>{ profile.getDifficultyRatingLabel(climb.difficulty) }</td>
                <td>{ when }</td>
                <td>
                    <Link to={ "/record#" + JSON.stringify(climb) } className="link-button compact">
                        <FontAwesomeIcon icon={faArrowRight} className="icon" />
                    </Link>
                </td>
            </tr>; 
        rows.push(row); 
    }

    return (
        <table className="listing" cellPadding={5} cellSpacing={0}>
            <thead>
                <tr>
                    <th>Wall</th>
                    <th>Result</th>
                    <th>Attempt</th>
                    <th>Streak</th>
                    <th>Rating</th>
                    <th>When</th>
                    <th>Repeat</th>
                </tr>
            </thead>
            <tbody>
                { rows }
            </tbody>
        </table>
    ); 
}

export default ClimbHistoryListing; 