import './ResponsiveGrid.css';  // loader embeds the CSS into the page (classes are globally available)

export interface IResponsiveGridProps {
    /** 2 to 12, defaults to 3 */
    colsLandscape?: number; 
    /** 1 to 8, defaults to 1 */
    colsPortrait?: number; 
    /** All items */
    children?: React.ReactNode; 
    /** For gallery tiles */
    compact?: boolean; 
    /** Set if the content shall also break into vertical flow if little horizontal space is available (as opposed to only for portrait layout). */
    wideContent?: boolean; 
}

/** Displays a container with columns, where the number of columns depends on the format of the screen. */
function ResponsiveGrid(props: IResponsiveGridProps) {
    // see ResponsiveGrid.css
    return (
        <div className={ "responsive-grid landscape-" + (props.colsLandscape || 3) + " portrait-" + (props.colsPortrait || 1) + (props.compact ? " compact" : '') + (props.wideContent ? " wide-content" : '') }>
            { props.children }
        </div>
    ); 
}

export default ResponsiveGrid; 
