import { CanvasJSChart, CanvasJS } from 'canvasjs-react-charts'; 
import React from 'react';

export interface IPieChartProps {
    values: number[]; 
    labels: string[]; 
    colorSet?: 'blackWhite' | 'redGreen' | 'colorSet1' | 'colorSet2' | 'colorSet3'; 
    title?: string; 
    size?: number; 
}

/** Displays the ratio of two or more values as a pie chart */
function PieChart(props: IPieChartProps) {
    // (see https://canvasjs.com/docs/charts/chart-types/html5-pie-chart/ for how to use CanvasJS React Charts)

    const [ workaround, setWorkaround ] = React.useState(false); 

    const points = props.values.map((v, i) => ({ y: v, indexLabel: props.labels[i] })); 

    const chartOptions = {
        animationEnabled: true,	
        theme: 'light2', 
        width: props.size || 300, height: props.size || 300, 
        title: {
            text: props.title
        },
        data: [{
            type: "pie",
            showInLegend: false,
            dataPoints: points
        }], 
        colorSet: props.colorSet
    }; 

    // there is a bug in the charting component, generating container ids by timestamp, causing duplicate ids and missing charts
    // we need to wait a random amount of milliseconds before we can render :(
    if (workaround)
        return <CanvasJSChart options={chartOptions} />; 
    else {
        setTimeout(() => {
           setWorkaround(true);  
        }, Math.random() * 500); 
        return <div style={{ width: props.size || 300, height: props.size || 300 }}></div>; 
    }
}

CanvasJS.addColorSet('blackWhite', [ '#333333', '#eeeeee' ]); 
CanvasJS.addColorSet('redGreen', [ '#B5E61D', '#E6341C' ]); 

export default PieChart; 