import './FeedbackForm.css'; 
import { useState } from "react";

const SKILL_LEVEL = ["Beginner", "Intermediate", "Advanced", "Expert"];

const FeedbackForm = () => {
  const [name, updateName] = useState("");
  const [email, updateEmail] = useState("");
  const [skill_level, update_skill_level] = useState("");

  return (
    <div className='feedback-form-main'>
      <form
        onSubmit={(e) => {
          e.preventDefault();
          // doSomething();
        }}
      >

        <div className='form-item'>
          <label htmlFor="name" className='large-label'>Name</label>
          <input id="name" type="text" value={name} placeholder="Name" onChange={(e) => updateName(e.target.value)}/>
        </div>

        <div className='form-item'>
          <label htmlFor="email" className='large-label'>Email</label>
          <input id="email" type="email" value={email} placeholder="name@example.com" onChange={(e) => updateEmail(e.target.value)}/>
        </div>

        <div className='form-item'>
          <label htmlFor="skill_level" className='large-label'>Skill Level</label>
          <select
            id="skill_level"
            value={skill_level}
            onChange={(e) => {
              update_skill_level(e.target.value);
            }}
            onBlur={(e) => {
              update_skill_level(e.target.value);
            }}
            >
              <option />
              {SKILL_LEVEL.map((skill_level) => (
                <option key={skill_level} value={skill_level}> {skill_level} </option>
              ))}
          </select>
        </div>

        <div className='form-item'>
          <fieldset>
            <legend className='large-label'>Are you a Climbify member?</legend>
            <input type="radio" name="member" id="yes" value="yes" />
            <label htmlFor="yes" className='small-label'>Yes</label>
            <input type="radio" name="member" id="no" value="no" defaultChecked />
            <label htmlFor="no" className='small-label'>No</label>
          </fieldset>
        </div>

        <div className='full-width form-item'>
          <label htmlFor="message" className='large-label'>Message</label>
          <textarea id="message" placeholder="Leave your suggestions "></textarea>
        </div>

        <div className="full-width form-item">
          <p className='large-label'>Your preferred climbing style:</p>
          <div>
            <input type="checkbox" id="bouldering" />
            <label htmlFor="bouldering" className='small-label'>Bouldering</label>
          </div>
          <div>
            <input type="checkbox" id="toproping" />
            <label htmlFor="toproping" className='small-label'>Top Roping</label>
          </div>
          <div>
            <input type="checkbox" id="leading" />
            <label htmlFor="leading" className='small-label'>Leading</label>
          </div>
        </div>

        <div className="full-width form-item">
          <button type="submit">Send Suggestions</button>
          <button type="reset">Clear Form</button>
        </div>

      </form>
      
    </div>
  );

};

export default FeedbackForm;