import { CanvasJSChart } from 'canvasjs-react-charts'; 
import Profile from '../Profile';
import { UserData } from '../UserData';

/** Displays the progress for all profiles as a line chart */
function ProgressChart() {
    // (see https://canvasjs.com/react-charts/multiseries-chart/ for how to use CanvasJS React Charts)

    // transform the climbs from each profile into curves with data points: 
    // we have to add points (rise on y-axis) if the climb was successful and flatline if not, 
    // with max. 10 points per level (32 levels reach a height of 320). 
    // Should the user have more than 10 successful climbs on a level, the curve will flatline (no progress). 
    const curves = 
        UserData.getProfiles().map(name =>  // for every profile
            climbsToCurve(new Profile(name))  // aggregate climbs
        ).filter(c => c.dataPoints.length > 0); 

    // determine y-range: 
    let maxLevel = 0, minLevel = 330; 
    for (const curve of curves)
        for (const point of curve.dataPoints) {
            if (point.y > maxLevel)
                maxLevel = point.y; 
            if (point.y < minLevel)
                minLevel = point.y; 
        }

    const chartOptions = {
        animationEnabled: true,	
        //exportEnabled: true, 
        //zoomEnabled: true, zoomType: 'xy', 
        theme: 'light2', 
        height: 300, 
        // title: {
        //     text: "Progress"
        // },
        axisY: {
            title: "Success", 
            viewportMaximum: maxLevel + 1, 
            viewportMinimum: minLevel - 1, 
            stripLines: [
                { value: 1*10, label: 'Novice', color: '#198019', labelFontColor: '#198019' }, 
                { value: 4*10, label: 'Beginner', color: '#3B8019', labelFontColor: '#3B8019' }, 
                { value: 8*10, label: 'Intermediate', color: '#5E8019', labelFontColor: '#5E8019' }, 
                { value: 13*10, label: 'Advanced', color: '#808019', labelFontColor: '#808019' }, 
                { value: 18*10, label: 'Expert', color: '#805E19', labelFontColor: '#805E19' }, 
                { value: 22*10, label: 'Super Expert', color: '#803B19', labelFontColor: '#803B19' }, 
                { value: 24*10, label: 'Elite', color: '#801919', labelFontColor: '#801919' }, 
                { value: 28*10, label: 'Super Elite', color: '#5D1880', labelFontColor: '#5D1880' }, 
                { value: 32*10, label: 'Alien', color: '#173680', labelFontColor: '#173680' }, 
            ]
        },
        axisX: {
            maximum: new Date(new Date().valueOf() + 1000*60*60*24*14),  // shift
            viewportMinimum: new Date(new Date().valueOf() - 1000*60*60*24*170),  // shift
        }, 
        toolTip: {
            shared: false
        },
        data: curves, 
    }; 

    return (
        <CanvasJSChart 
            options={chartOptions} 
         // onRef={ ref => this.chart = ref }
        />
    ); 


    function climbsToCurve(profile: Profile) {
        // climbs are sorted by date added (chronologically). 
        // only successful climbs make the line rise, and only by max. 10 points per level (32 levels in total). 
        const points = []; 
        let currentLevel = 0; 
        let currentScore = 0; 
        const allClimbs = profile.getClimbs(); 
        const dtThreshold = new Date(new Date().valueOf() - 1000*60*60*24*180);  // last 90 days
        const recentClimbs = allClimbs.filter(c => c.date > dtThreshold); 
        for (const climb of recentClimbs) {
            if (climb.difficulty !== currentLevel) {
                currentLevel = climb.difficulty; 
                currentScore = 0; 
            }
            if (climb.successful)
                currentScore++; 
            const y = Math.max(0, currentLevel - 1) * 10 + Math.min(currentScore, 10); 
            points.push({ x: climb.date, y: y, label: "Level " +   Profile.current.getDifficultyRatingLabel(currentLevel)  + ", Score " + currentScore + " of 10" }); 
        }
        return {
            type: "line",
            toolTipContent: "{x}: {label}", 
            name: profile.name,
            showInLegend: true,
            xValueFormatString: "DD MMM YYYY",
            dataPoints: points
        }
    }
}

export default ProgressChart; 